@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
nav{position:fixed;z-index:1;display:grid;grid-template-columns:repeat(6, 1fr);grid-auto-rows:auto;grid-gap:1em;justify-content:space-around;align-items:center;min-height:5vh;padding:1em}.nav-links{grid-column:6/6;display:grid;grid-template-columns:repeat(4, 1fr);grid-gap:1em;padding:0.5em;width:10%;font-size:28px}.nav-links li{list-style:none}.nav-links a:hover{color:#BF6464;transition:0.3s;border-bottom:3px dotted}.nav-links a.active{color:#BF6464;transition:0.3s;border-bottom:3px dotted}.nav-links a{text-decoration:none;transition:0.3s}

section{position:relative;height:100vh;background-color:#2A2131}section:nth-of-type(4n){background:linear-gradient(#2A2131, #372b40, #BF6464)}.item-header{justify-self:start;grid-column-start:1;grid-column-end:13;grid-row-start:1;grid-row-end:1;min-height:8vh;padding:0.5em;font-size:32px}.gridcontainer{display:grid;height:100%;width:100%;grid-template-columns:repeat(12, 1fr);grid-template-rows:repeat(12, 1fr)}.item-main{grid-column-start:1;grid-column-end:13;grid-row-start:1;grid-row-end:13;align-self:center;justify-self:center;text-align:center}.item-carousel{grid-column-start:1;grid-column-end:13;grid-row-start:5;display:flex}.item-introduction{grid-column-start:1;grid-column-end:13;grid-row-start:1;grid-row-end:13;align-self:center;justify-self:center;text-align:center}

.tooltip{position:relative;display:inline-block}.tooltip .tooltiptext{opacity:0;width:120px;background:linear-gradient(to bottom right, #bd1abd, #BF6464 70%);padding:5px 0;border-radius:6px;color:white;text-align:center;font-size:14px;position:absolute;z-index:1;width:120px;top:100%;left:50%;box-shadow:0 1px 1px 0 rgba(0,0,0,0.14),0 2px 1px -1px rgba(0,0,0,0.12),0 1px 3px 0 rgba(0,0,0,0.2);margin-left:-60px;transition:0.3s;visibility:hidden}.tooltip .tooltiptext::after{content:" ";position:absolute;bottom:100%;left:50%;margin-left:-5px;border-width:5px;border-style:solid;border-color:transparent transparent #BF6464 transparent}.tooltip:hover .tooltiptext{transition:0.3s;opacity:1;visibility:visible}

i{color:#BF6464;padding-right:0.25em;padding-left:0.25em;transition:0.5s;text-shadow:0px 7px #2A2131}i:hover{transition:0.3s;color:white;cursor:pointer}

.card{position:relative;background-color:rgba(170,70,70,0.05);background-size:contain;background-repeat:no-repeat;background-position:center;width:60vmin;height:60vmin;box-shadow:0 1px 1px 0 rgba(0,0,0,0.14),0 2px 1px -1px rgba(0,0,0,0.12),0 1px 3px 0 rgba(0,0,0,0.2);display:grid;grid-auto-rows:1fr;justify-content:space-evenly;align-items:center;grid-gap:1em;border-radius:10%}@media only screen and (max-width: 1024px){.card{position:relative;background-color:rgba(170,70,70,0.05);background-size:contain;background-repeat:no-repeat;background-position:center;width:70vmin;height:80vh;box-shadow:0 1px 1px 0 rgba(0,0,0,0.14),0 2px 1px -1px rgba(0,0,0,0.12),0 1px 3px 0 rgba(0,0,0,0.2);display:grid;grid-auto-rows:1fr;justify-content:space-evenly;align-items:center;grid-gap:1em;border-radius:10%}}.card-info{width:100%;height:100%;position:absolute;background:#BF6464;padding:inherit;border-radius:inherit;display:grid;grid-template-rows:repeat(7, 1fr);grid-template-columns:repeat(1, 1fr);-webkit-clip-path:circle(5% at 50% 0%);clip-path:circle(5% at 50% 0%);transition:all .5s ease-in-out;cursor:pointer}.card-info:hover{-webkit-clip-path:circle(100%);clip-path:circle(100%);background:#BF6464;opacity:0.95;z-index:2}.card-info-element{padding:1em}.card-icons{display:grid;grid-template-columns:repeat(2, 1fr);margin:auto;grid-gap:3em}

*{padding:0px;margin:0px;color:white;font-family:'Poppins', sans-serif;scroll-behavior:smooth}html{overflow-x:hidden;overflow-y:scroll}.frontParagraph{text-align:center}.BrainhubCarousel__arrows{background-color:#BF6464}.BrainhubCarousel__arrows:hover{transition:0.3s;background-color:!important white;cursor:pointer}.BrainhubCarousel__arrows:enabled{background-color:#BF6464}.BrainhubCarousel__dot{background-color:#BF6464}.container{position:relative;background-color:#1E1824;padding:1em;margin:1em;box-shadow:0 1px 1px 0 rgba(0,0,0,0.14),0 2px 1px -1px rgba(0,0,0,0.12),0 1px 3px 0 rgba(0,0,0,0.2);border-radius:2px}.circle-container{position:relative;background-color:#1E1824;padding:1em;margin:1em;border-radius:50%;width:50vmin;height:50vmin;display:flex;justify-content:center;align-items:center;flex-direction:column;z-index:1;transform-style:preserve-3d}.circle-container::before{content:'';position:absolute;border-radius:50%;width:30vmin;height:30vmin;padding:1em;margin:1em;right:25vmin;background-color:#2A2131;z-index:-1;transform:translateZ(-1px);-webkit-clip-path:polygon(20% 0%, 80% 0%, 100% 100%, 0% 100%);clip-path:polygon(20% 0%, 80% 0%, 100% 100%, 0% 100%);animation:polygons 60s alternate infinite}.circle-container::after{content:'';position:absolute;width:40vmin;height:40vmin;padding:1em;margin:1em;left:25vmin;background-color:#BF6464;z-index:-1;transform:translateZ(-1px);-webkit-clip-path:polygon(20% 0%, 80% 0%, 100% 100%, 0% 100%);clip-path:polygon(20% 0%, 80% 0%, 100% 100%, 0% 100%);animation:polygons 60s alternate infinite}.vertical-overlap{display:flex}.accent-background{background-color:#BF6464}.primary-dark-background{background-color:#2A2131}@keyframes polygons{25%{-webkit-clip-path:polygon(20% 0%, 100% 38%, 70% 90%, 0% 100%);clip-path:polygon(20% 0%, 100% 38%, 70% 90%, 0% 100%);background:#2A2131}50%{-webkit-clip-path:polygon(0 46%, 100% 15%, 55% 74%, 0 100%);clip-path:polygon(0 46%, 100% 15%, 55% 74%, 0 100%);background:#BF6464}75%{-webkit-clip-path:polygon(100% 38%, 100% 38%, 66% 100%, 0 53%);clip-path:polygon(100% 38%, 100% 38%, 66% 100%, 0 53%);background:cornflowerblue}}

.center{position:relative;top:50%;left:50%;transform:translate(-50%, -50%)}.center-bottom{position:relative;top:100vh;left:50vw;transform:translate(-50%, -100%)}.center-top{position:absolute;left:50vw;transform:translate(-50%, -100%)}.huge{font-size:48px}.accent-style{color:#BF6464}

