@import './colors.scss';
  
  
  section {
    position: relative;
    height: 100vh;
    background-color: $primary-color-dark;
  }

  section:nth-of-type(4n){
    background: linear-gradient( $primary-color-dark, lighten($primary-color-dark, 5%),$accent-color);
  }

  
  .item-header{
    justify-self: start;
    grid-column-start: 1;
    grid-column-end: 13;
    grid-row-start: 1;
    grid-row-end: 1;
    min-height: 8vh;
    padding: 0.5em;
    font-size: 32px;
  }


  .gridcontainer{
      display: grid;
      height: 100%;
      width: 100%;
      grid-template-columns: repeat(12, 1fr);
      grid-template-rows: repeat(12, 1fr);
  }

  .item-main{
    grid-column-start: 1;
    grid-column-end: 13;
    grid-row-start: 1;
    grid-row-end: 13;
    align-self: center;
    justify-self: center;
    text-align: center;
  }

  .item-carousel{
    grid-column-start: 1;
    grid-column-end: 13;
    grid-row-start: 5;
    display: flex;
  }

  .item-introduction{
    grid-column-start: 1;
    grid-column-end: 13;
    grid-row-start: 1;
    grid-row-end: 13;
    align-self: center;
    justify-self: center;
    text-align: center;
  }